                        <el-card shadow="always">
                            <!-- <div class="cardtitle">专项经费同比变化图</div> -->
                            <barCharts v-if="char" :op="baroption3"></barCharts>
                        </el-card>
                    </el-col>
                    <el-col class="coltips">
                        <el-card class="tipheight" shadow="always">
                            <div class="tips">说明:</div>
                            <p class="tippoint">根据教育部2010年《中等职业学校设置标准(教职成[2010]12号)》文件要求，中职学校占地面积<span style="color:red;">不少于40000m²</span>。</p>
                        </el-card>
                    </el-col>
                </el-row>
                <el-row class="row" :gutter="12">
                    <el-col class="col">
                        <el-card shadow="always">
                            <!-- <div class="cardtitle">经费开支分类堆积柱状图</div> -->
                            <barCharts v-if="char" :op="baroption4"></barCharts>
                        </el-card>
                    </el-col>
                    <el-col class="coltips">
                        <el-card class="tipheight" shadow="always">
                            <div class="tips">说明:</div>
                            <p class="tippoint">根据教育部2010年《中等职业学校设置标准(教职成[2010]12号)》文件要求，中职学校占地面积<span style="color:red;">不少于40000m²</span>。</p>
                        </el-card>
                    </el-col>
                </el-row>
            </el-tab-pane>
            <el-tab-pane label="教学设备投入金额同比变化图" name="second" lazy>
                <el-row class="row" :gutter="12">
                    <el-col class="col">
                        <el-card shadow="always">
                            <!-- <div class="cardtitle">教学设备投入金额同比变化图</div> -->
                            <barCharts v-if="char" :op="baroption5"></barCharts>
                        </el-card>
                    </el-col>
                    <el-col class="coltips">
                        <el-card class="tipheight" shadow="always">
                            <div class="tips">说明:</div>
                            <p class="tippoint">根据教育部2010年《中等职业学校设置标准(教职成[2010]12号)》文件要求，中职学校占地面积<span style="color:red;">不少于40000m²</span>。</p>
                        </el-card>
                    </el-col>
                </el-row>
            </el-tab-pane>
            <el-tab-pane label="办学经费支出分类饼图" name="third" lazy>
                <el-row class="row" :gutter="12">
                    <el-col class="col">
                        <el-card shadow="always">
                            <!-- <div class="cardtitle">办学经费支付分类饼图</div> -->
                            <pieCharts v-if="char" :op="baroption6"></pieCharts>
                        </el-card>
                    </el-col>
                    <el-col class="coltips">
                        <el-card class="tipheight" shadow="always">
                            <div class="tips">说明:</div>
                            <p class="tippoint">根据教育部2010年《中等职业学校设置标准(教职成[2010]12号)》文件要求，中职学校占地面积<span style="color:red;">不少于40000m²</span>。</p>
                        </el-card>
                    </el-col>
                </el-row>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import barCharts from './barCharts'
import pieCharts from './Pie'
export default {
    name: 'bxjf',
    data(){
        return{
            activeName: 'first',
            char:false,
            xData:[],
            yData:[],
            legend:[],
            pielegend:[],
            baroption1:{
                id:'chart1',
                title: '总经费同比变化图',
                param:[],
                btn:'1',
                xAxis: [
                    {
                        data: [],
                        axisLabel: {
                            rotate:0,
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                    }
                ],
                series: [{
                    name: '人数',
                    data: [],
                }]
            },
            baroption2:{
                id:'chart2',
                title: '生均经费同比变化图',
                param:[],
                dm:[],
                btn:'1',
                xAxis: [
                    {
                        data: ['信息工程系', '机电工程汽车系', '人文系', '财贸经济系', '外语系', '规划建筑工程系', '园林与食品工程学院', '互联网学院', '艺术系'],
                        axisLabel: {
                            rotate:0,
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                    }
                ],
                series: [{
                    name: '人数',
                    data: [1570, 1300, 1146, 915, 710, 616, 500, 415, 320],
                }]
            },
            baroption3:{
                id:'chart3',
                title: '专项经费同比变化图',
                param:[],
                btn:'1',
                xAxis: [
                    {
                        data: ['信息工程系', '机电工程汽车系', '人文系', '财贸经济系', '外语系', '规划建筑工程系', '园林与食品工程学院', '互联网学院', '艺术系'],
                        axisLabel: {
                            rotate:0,
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                    }
                ],
                series: [{
                    name: '人数',
                    data: [1570, 1300, 1146, 915, 710, 616, 500, 415, 320],
                }]
            },
            baroption4:{
                id:'chart4',
                title: '经费开支分类堆积柱状图',
                param:[],
                btn:'1',
                legend: {
                    data:['教学改革及研究费','设备购置','师资培训','图书购置','其它','学生培训费用','学校文化建设','学校建筑修缮'],
                    right: '10%',
                    bottom:'0',
                },
                xAxis : [
                    {
                        data : [],
                        axisLabel: {
                            rotate:0,
                        }
                    }
                ],
                yAxis : [
                    {
                        name:'(人)',
                    }
                ],
                series : [
                    {
                        name:'教学改革及研究费',
                        type:'bar',
                        stack: '开支',
                        data:[]
                    },
                    {
                        name:'设备购置',
                        type:'bar',
                        stack: '开支',
                        data:[]
                    },
                    {
                        name:'师资培训',
                        type:'bar',
                        stack: '开支',
                        data:[]
                    },
                    {
                        name:'图书购置',
                        type:'bar',
                        stack: '开支',
                        data:[]
                    },
                    {
                        name:'其它',
                        type:'bar',
                        stack: '开支',
                        data:[]
                    },
                    {
                        name:'学生培训费用',
                        type:'bar',
                        stack: '开支',
                        data:[]
                    },
                    {
                        name:'学校文化建设',
                        type:'bar',
                        stack: '开支',
                        data:[]
                    },
                    {
                        name:'学校建筑修缮',
                        type:'bar',
                        stack: '开支',
                        data:[]
                    }
                ]
            },
            baroption5:{
                id:'chart5',
                title: '教学设备投入金额同比变化图',
                param:[],
                btn:'1',
                xAxis: [
                    {
                        data: ['信息工程系', '机电工程汽车系', '人文系', '财贸经济系', '外语系', '规划建筑工程系', '园林与食品工程学院', '互联网学院', '艺术系'],
                        axisLabel: {
                            rotate:0,
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                    }
                ],
                series: [{
                    name: '人数',
                    data: [1570, 1300, 1146, 915, 710, 616, 500, 415, 320],
                }]
            },
            baroption6:{
                id: 'chart6',
                title:'办学经费支付分类饼图',
                param:[],
                btn:'1',
                tooltip : {
                    formatter: "{a} <br/>{b} : {c}万元 ({d}%)"
                },
                legend: {
                    data: []
                },
                color:['rgb(80,80,80)','rgb(124,179,73)','rgb(118,131,149)','rgb(53,143,226)','rgb(228,39,57)','rgb(128,146,149)','rgb(239,134,41)','rgb(240,240,240)',],
                series : [
                    {
                        name: '开支',
                        data:[
                            {value:'', name:''},
                            {value:'', name:''},
                            {value:'', name:''},
                            {value:'', name:''},
                            {value:'', name:''},
                            {value:'', name:''},
                            {value:'', name:''},
                            {value:'', name:''},
                        ],
                        itemStyle: {
                            normal:{
                                label:{
                                    formatter: "{b} :\n{c}万元 \n({d}%)",
                                }
                            },
                        }
                    }
                ]
            },
        }
    },
    components: {
        barCharts,
        pieCharts
    },
    methods: {
        handleClick(tab, event) {
            // console.log(tab, event);
        }
    },
    mounted() {
        var _this = this
        $.ajax({
            type: "post",
            url: _this.baseUrl+"/ydjxzg/hx/xsgl.action",
            data: {reg:'xxbxjf',style:'1'},
            dataType: "json",
            success: function (res) {
                console.log(res)
                if(res.bxzjf){
                    _this.xData = []
                    _this.yData = []
                    //_this.baroption1.param.push(res.bxzjf[0].bl1)
                    for(var i = 1;i < res.bxzjf.length;i++){
                        _this.xData.push(res.bxzjf[i].mc)
                        _this.yData.push(res.bxzjf[i].zcount)
                    }
                    // this.baroption1.yAxis[0].name = '㎡'
                    _this.baroption1.xAxis[0].data = _this.xData;
                    _this.baroption1.series[0].data = _this.yData;
                }
                if(res.sjjftbt){
                    _this.xData = []
                    _this.yData = []
                    //_this.baroption2.param.push(res.sjjftbt[0].bl1)
                    for(var i = 1;i < res.sjjftbt.length;i++){
                        _this.xData.push(res.sjjftbt[i].mc)
                        _this.yData.push(res.sjjftbt[i].zcount)
                        if(res.sjjftbt[i].dm){
                            _this.baroption2.dm.push(res.sjjftbt[i].dm)
                        }
                    }
                    // this.baroption1.yAxis[0].name = '㎡'
                    _this.baroption2.xAxis[0].data = _this.xData;
                    _this.baroption2.series[0].data = _this.yData;
                }
                if(res.zyjftbt){
                    _this.xData = []
                    _this.yData = []
                    //_this.baroption3.param.push(res.zyjftbt[0].bl2)
                    for(var i = 1;i < res.zyjftbt.length;i++){
                        _this.xData.push(res.zyjftbt[i].mc)
                        _this.yData.push(res.zyjftbt[i].zj)
                        if(res.zyjftbt[i].dm){
                            _this.baroption3.dm.push(res.zyjftbt[i].dm)
                        }
                    }
                    // this.baroption1.yAxis[0].name = '㎡'
                    _this.baroption3.xAxis[0].data = _this.xData;
                    _this.baroption3.series[0].data = _this.yData;
                }
                if(res.jfkzdjt){
                    _this.xData = []
                    _this.yData = []
                    _this.baroption4.param.push(res.jfkzdjt[0].bl1)
                    for(var i = 1;i < res.jfkzdjt.length;i++){
                        _this.xData.push(res.jfkzdjt[i].nf)
                        for(var j = 0;j < res.jfkzdjt[i].tjList.length;j++){
                            _this.baroption4.series[j].data.push(res.jfkzdjt[i].tjList[j].zcount)
                            //_this.baroption4.series[i].name = res.jfkzdjt[i].tjList[j].mc
                        }
                    }
                    _this.baroption4.xAxis[0].data = _this.xData
                    console.log(_this.baroption4)
                }
               if(res.jxtrjetbt){
                    _this.xData = []
                    _this.yData = []
                    //_this.baroption5.param.push(res.jxtrjetbt[0].bl1)
                    for(var i = 0;i < res.jxtrjetbt.length;i++){
                        _this.xData.push(res.jxtrjetbt[i].mc)
                        _this.yData.push(res.jxtrjetbt[i].zcount)
                        if(res.jxtrjetbt[i].dm){
                            _this.baroption5.dm.push(res.jxtrjetbt[i].dm)
                        }
                    }
                    // this.baroption1.yAxis[0].name = '㎡'
                    _this.baroption5.xAxis[0].data = _this.xData;
                    _this.baroption5.series[0].data = _this.yData;
                }
                if(res.bxjfzcflbt){             //办学经费饼图
                    //_this.baroption6.param.push(res.bxjfzcflbt[0].title)
                    for(var i = 1;i < res.bxjfzcflbt.length;i++){
                        _this.pielegend.push(res.bxjfzcflbt[i].mc)
                        _this.baroption6.series[0].data[i-1].value = res.bxjfzcflbt[i].zcount
                        _this.baroption6.series[0].data[i-1].name = res.bxjfzcflbt[i].mc
                    }
                     //_this.baroption6.param.push(data.param)
                     //_this.baroption6.title = data.title
                     _this.baroption6.series[0].name = '万元'
                    _this.baroption6.legend = _this.pielegend
                }
                
                _this.char = true
            },
            error:function(err){
                console.log(err)
            }
        });
        
    },
}
</script>

<style>
#banxue{
    width: 100%;
    height: 100%;
}
#banxue .row{
    display:flex;
    justify-content:center;
}
#banxue .el-tabs--left .el-card__body{
    padding: 0!important;
}
#banxue .row .col{
    width:750px;
    margin-top:20px;
}
#banxue .row .cardtitle{
    font-size:25px;
    text-align:center;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
}
#banxue .row .coltips{
    min-width:280px;
    max-width:300px;
    margin-top:20px;
}
#banxue .row .coltips2{
    min-width:150px;
    max-width:300px;
    margin-top:20px;
}
#banxue .row .tipheight{
    height:435px;
}
#banxue .row .tip_select{
    height:465px;
}
#banxue .row .tips{
    font-size:25px;
    text-align:left;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
    padding:0 20px;
}
#banxue .row .tippoint{
    margin-top:50px;
    padding:0 20px;
    line-height:40px;
}
#banxue .is-active{
    color: #029348 !important;
}
.el-tabs__active-bar{
    background: #029348 !important;
}
#banxue .is-top :hover{
    color: #029348 !important;
}
#banxue .is-left :hover{
    color: #029348 !important;
}
#banxue .el-card{
    padding: 40px 0 40px 0;
}
#banxue .shaixaun{
    padding: 82px 0 32px 0;
}
.el-tabs__content{
    overflow:inherit;
    padding-bottom: 20px;
}
.el-card{
    transition: 0s;
}
</style>



